import React from 'react'

import { Button, Result } from "antd";


function Page404(props) {
    const backHome = () => {
        props.history.push('/')
    }

    return (
        <>
            <Result
                status="404"
                title="404"
                subTitle="很抱歉，你反问的页面不存在！"
                extra={<Button type="primary" onClick={backHome}>回到首页</Button>}
            />
        </>
    )

}

export default Page404
